const baseUrl = "https://tailup.love" // --public
const videoDataAppId = "1305387564"
const postBackUrl = "http://1305387564.vod2.myqcloud.com/6b095ff3vodsh1305387564/cee1cd50387702304954623462/PynoKA7N67wA.png"
// const baseUrl = "http://test.tailup.love" // --public
// const videoDataAppId = "1500007875"
// const postBackUrl = "http://1500007875.vod2.myqcloud.com/6c9b6a63vodcq1500007875/551e12b4387702304925577532/VgYY045euu4A.png"

import {
	getParam
} from './util.js'

import MyPromise from "./promise/es6-promise"

import store from '../store/index';
// console.log(store)

const headers = {
	'Content-Type': 'application/json;charset=UTF-8',
	'Accept': 'application/json',
}

function dealResponse(res, config = {}, resolve, reject, url) {
	if (res.statusCode === 200) {
		// console.log('axios--res', res)
		if (res.header && res.header['Access-Token']) {
			store.commit('user/login', res.header['Access-Token'])
		}
		if (res.data.code !== 200) {
			uni.showToast({
				title: res.data.msg || "",
				icon: 'none',
				duration: 2000, //1500
				mask: false, //false
			})
			reject(res)
		} else {
			// uni.hideToast({
			//   success: () => { },
			//   fail: () => { }
			// });
			resolve(res.data)
		}
	} else if (res.statusCode === 404) {
		uni.showToast({
			title: "",
			icon: 'none',
			duration: 2000, //1500
			mask: true, //false
		})
		throwError('404:' + url);
		reject(res)
	}
}

function throwError(str) {
	setTimeout(() => {
		throw new Error(str);
	})
}

module.exports = {
	baseUrl,
	videoDataAppId,
	get(url, payload, config = {}) {
		var token = store.state.user.token;
		if (token && (token.length > 0)) {
			headers['Access-Token'] = `${token}`
		}
		// headers['Content-Type'] = 'x-www-form-urlencoded';
		headers['Content-Type'] = 'application/json';


		let params = getParam(payload)
		return new MyPromise((resolve, reject) => {
			uni.request({
				url: baseUrl + url + (params ? `?${params}` : ""),
				method: "GET",
				header: headers,
				timeout: 60000,
				success: function(res) {
					dealResponse(res, config, resolve, reject, url)
				},
				fail: function(err) {
					uni.showToast({
						title: "",
						icon: 'none',
						duration: 2000, //1500
						mask: false, //false
					})
					console.log(err)
					reject(err)
					// dealResponse(res, config, resolve, reject)
				}
			})
		})
	},
	post(url, payload, config = {}) {
		var token = store.state.user.token;
		if (token && (token.length > 0)) {
			headers['Access-Token'] = `${token}`
		}
		if (config['Content-Type']) {
			headers['Content-Type'] = config['Content-Type'];
		} else {
			headers['Content-Type'] = 'application/json;charset=UTF-8';
		}
		return new MyPromise((resolve, reject) => {
			uni.request({
				url: baseUrl + url,
				method: "POST",
				data: payload,
				timeout: 60000,
				header: headers,
				success: function(res) {
					dealResponse(res, config, resolve, reject, url)
				},
				fail: function(err) {
					uni.showToast({
						title: "",
						icon: 'none',
						duration: 2000, //1500
						mask: false, //false
					})
					console.log(err)
					reject(err)
					// dealResponse(res, config, resolve, reject)
				}
			})
		})
	},
}
